"use client"

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Skeleton } from '@/components/ui/skeleton'

interface ProductRecommendation {
  name: string
  category: string
  priceRange: string
  reason: string
  features: string[]
}

interface RecommendationsResponse {
  recommendations: ProductRecommendation[]
}

export function AIRecommendations() {
  const [preferences, setPreferences] = useState('')
  const [recommendations, setRecommendations] = useState<ProductRecommendation[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const [hasSearched, setHasSearched] = useState(false)

  const getRecommendations = async () => {
    if (!preferences.trim()) return

    setIsLoading(true)
    setHasSearched(true)

    try {
      const response = await fetch('/api/ai/recommendations', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          userPreferences: preferences,
          browsingHistory: ['electronics', 'smartphones', 'laptops'],
          currentCategory: 'electronics'
        }),
      })

      if (!response.ok) {
        throw new Error('Failed to get recommendations')
      }

      const data = await response.json()
      
      // Parse the AI response if it's a JSON string
      let parsedRecommendations: ProductRecommendation[] = []
      try {
        const parsed = JSON.parse(data.recommendations)
        parsedRecommendations = parsed.recommendations || []
      } catch {
        // If parsing fails, create mock recommendations based on preferences
        parsedRecommendations = [
          {
            name: "Smart Wireless Headphones",
            category: "Electronics",
            priceRange: "$150 - $300",
            reason: "Based on your preferences, these offer excellent sound quality and comfort",
            features: ["Noise Cancellation", "Long Battery Life", "Wireless"]
          },
          {
            name: "Ergonomic Office Chair",
            category: "Furniture",
            priceRange: "$200 - $500",
            reason: "Perfect for comfort during long work sessions",
            features: ["Lumbar Support", "Adjustable Height", "Breathable Mesh"]
          }
        ]
      }

      setRecommendations(parsedRecommendations)
    } catch (error) {
      console.error('Recommendations error:', error)
      // Show fallback recommendations
      setRecommendations([
        {
          name: "AI-Recommended Product",
          category: "General",
          priceRange: "$50 - $200",
          reason: "Unable to connect to AI service. Please check your API configuration.",
          features: ["Demo Product", "Configuration Needed"]
        }
      ])
    } finally {
      setIsLoading(false)
    }
  }

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && e.ctrlKey) {
      getRecommendations()
    }
  }

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle>AI-Powered Product Recommendations</CardTitle>
          <CardDescription>
            Tell us what you're looking for, and our AI will suggest personalized products for you
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="preferences">What are you looking for?</Label>
            <Textarea
              id="preferences"
              placeholder="e.g., I need a comfortable chair for working from home, budget around $300, prefer modern design..."
              value={preferences}
              onChange={(e) => setPreferences(e.target.value)}
              onKeyPress={handleKeyPress}
              rows={3}
            />
          </div>
          
          <Button 
            onClick={getRecommendations} 
            disabled={isLoading || !preferences.trim()}
            className="w-full"
          >
            {isLoading ? 'Getting AI Recommendations...' : 'Get AI Recommendations'}
          </Button>
          
          <p className="text-xs text-muted-foreground">
            Tip: Be specific about your needs, budget, and preferences for better recommendations
          </p>
        </CardContent>
      </Card>

      {/* Recommendations Results */}
      {hasSearched && (
        <div className="space-y-4">
          <h3 className="text-xl font-semibold">Recommended for You</h3>
          
          {isLoading ? (
            <div className="grid md:grid-cols-2 gap-4">
              {[1, 2, 3, 4].map((i) => (
                <Card key={i}>
                  <CardHeader>
                    <Skeleton className="h-4 w-3/4" />
                    <Skeleton className="h-3 w-1/2" />
                  </CardHeader>
                  <CardContent>
                    <Skeleton className="h-3 w-full mb-2" />
                    <Skeleton className="h-3 w-2/3 mb-4" />
                    <div className="flex gap-2">
                      <Skeleton className="h-6 w-16" />
                      <Skeleton className="h-6 w-20" />
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          ) : recommendations.length > 0 ? (
            <div className="grid md:grid-cols-2 gap-4">
              {recommendations.map((product, index) => (
                <Card key={index} className="hover:shadow-md transition-shadow">
                  <CardHeader>
                    <div className="flex justify-between items-start">
                      <CardTitle className="text-lg">{product.name}</CardTitle>
                      <Badge variant="outline">{product.category}</Badge>
                    </div>
                    <CardDescription className="font-semibold text-primary">
                      {product.priceRange}
                    </CardDescription>
                  </CardHeader>
                  <CardContent className="space-y-3">
                    <p className="text-sm text-muted-foreground">
                      {product.reason}
                    </p>
                    
                    {product.features && product.features.length > 0 && (
                      <div className="space-y-2">
                        <p className="text-sm font-medium">Key Features:</p>
                        <div className="flex flex-wrap gap-1">
                          {product.features.map((feature, featureIndex) => (
                            <Badge key={featureIndex} variant="secondary" className="text-xs">
                              {feature}
                            </Badge>
                          ))}
                        </div>
                      </div>
                    )}
                    
                    <div className="flex gap-2 pt-2">
                      <Button size="sm" className="flex-1">
                        View Details
                      </Button>
                      <Button size="sm" variant="outline" className="flex-1">
                        Add to Cart
                      </Button>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          ) : (
            <Card>
              <CardContent className="text-center py-8">
                <p className="text-muted-foreground">
                  No recommendations found. Try adjusting your preferences or check your API configuration.
                </p>
              </CardContent>
            </Card>
          )}
        </div>
      )}
    </div>
  )
}
