"use client"

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Skeleton } from '@/components/ui/skeleton'

interface SmartSearchProps {
  onSearch?: (query: string) => void
}

export function SmartSearch({ onSearch }: SmartSearchProps) {
  const [query, setQuery] = useState('')
  const [searchResults, setSearchResults] = useState<string>('')
  const [isLoading, setIsLoading] = useState(false)
  const [hasSearched, setHasSearched] = useState(false)

  const handleSearch = async () => {
    if (!query.trim()) return

    setIsLoading(true)
    setHasSearched(true)
    onSearch?.(query)

    try {
      const response = await fetch('/api/ai/search', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          query,
          filters: {
            category: 'all',
            priceRange: 'any'
          }
        }),
      })

      if (!response.ok) {
        throw new Error('Failed to perform search')
      }

      const data = await response.json()
      setSearchResults(data.results)
    } catch (error) {
      console.error('Search error:', error)
      setSearchResults('Unable to connect to AI search service. Please check your API configuration and try again.')
    } finally {
      setIsLoading(false)
    }
  }

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      handleSearch()
    }
  }

  const suggestedSearches = [
    'wireless headphones under $200',
    'ergonomic office chair',
    'gaming laptop with RTX graphics',
    'smart home devices',
    'fitness tracker waterproof',
    'coffee maker with grinder'
  ]

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle>AI-Powered Smart Search</CardTitle>
          <CardDescription>
            Search using natural language - our AI understands what you're looking for
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="flex space-x-2">
            <Input
              placeholder="Try: 'wireless headphones under $200' or 'best laptop for gaming'"
              value={query}
              onChange={(e) => setQuery(e.target.value)}
              onKeyPress={handleKeyPress}
              className="flex-1"
            />
            <Button onClick={handleSearch} disabled={isLoading || !query.trim()}>
              {isLoading ? 'Searching...' : 'Search'}
            </Button>
          </div>

          <div className="space-y-2">
            <p className="text-sm font-medium">Try these searches:</p>
            <div className="flex flex-wrap gap-2">
              {suggestedSearches.map((suggestion, index) => (
                <Badge
                  key={index}
                  variant="outline"
                  className="cursor-pointer hover:bg-primary hover:text-primary-foreground transition-colors"
                  onClick={() => setQuery(suggestion)}
                >
                  {suggestion}
                </Badge>
              ))}
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Search Results */}
      {hasSearched && (
        <Card>
          <CardHeader>
            <CardTitle>Search Results</CardTitle>
            <CardDescription>
              AI-powered search results for: "{query}"
            </CardDescription>
          </CardHeader>
          <CardContent>
            {isLoading ? (
              <div className="space-y-3">
                <Skeleton className="h-4 w-full" />
                <Skeleton className="h-4 w-3/4" />
                <Skeleton className="h-4 w-1/2" />
                <div className="grid md:grid-cols-2 gap-4 mt-6">
                  {[1, 2, 3, 4].map((i) => (
                    <div key={i} className="space-y-2">
                      <Skeleton className="h-32 w-full" />
                      <Skeleton className="h-4 w-3/4" />
                      <Skeleton className="h-3 w-1/2" />
                    </div>
                  ))}
                </div>
              </div>
            ) : searchResults ? (
              <div className="space-y-4">
                <div className="prose prose-sm max-w-none">
                  <div className="whitespace-pre-wrap text-sm">
                    {searchResults}
                  </div>
                </div>
                
                {/* Mock Product Results */}
                <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-4 mt-6">
                  {[
                    {
                      name: "Premium Wireless Headphones",
                      price: "$179.99",
                      rating: "4.8/5",
                      image: "ðŸŽ§"
                    },
                    {
                      name: "Ergonomic Office Chair Pro",
                      price: "$299.99",
                      rating: "4.7/5",
                      image: "ðŸª‘"
                    },
                    {
                      name: "Gaming Laptop RTX 4060",
                      price: "$1,299.99",
                      rating: "4.9/5",
                      image: "ðŸ’»"
                    }
                  ].map((product, index) => (
                    <Card key={index} className="hover:shadow-md transition-shadow">
                      <CardContent className="p-4">
                        <div className="text-4xl text-center mb-3">{product.image}</div>
                        <h4 className="font-semibold text-sm mb-2">{product.name}</h4>
                        <div className="flex justify-between items-center text-sm">
                          <span className="font-bold text-primary">{product.price}</span>
                          <Badge variant="secondary">{product.rating}</Badge>
                        </div>
                        <Button size="sm" className="w-full mt-3">
                          View Product
                        </Button>
                      </CardContent>
                    </Card>
                  ))}
                </div>
              </div>
            ) : (
              <p className="text-muted-foreground text-center py-8">
                No results found. Try a different search query.
              </p>
            )}
          </CardContent>
        </Card>
      )}
    </div>
  )
}
